import React, { Component } from 'react'

import Logout from '../Buttons/Logout'
import axios from 'axios'

export class BlindStudentHome extends Component {
    _isMounted = false;
    state = {
        login: this.props.login,
        body: "",
        answerA: "",
        answerB: "",
        answerC: "",
        answerD: "",
        properAnswer: "",
        category: "",
        userAnswer: "",
        points: 0,
        totalPoints: 0,
        currentQuestion: 0,
        currentAnswer: 0,
        isAnswered: false,
        canReceiveTest: true,
        canHear: true,
        canSpeak: "",
        speakTotalQuestions: "",
        speakTotalPoints: "",
        option: ""
    }
    serveQuestion = (test) => {

        if (this.state.canReceiveTest) {
            this.setState({
                canSpeak: true,
                canReceiveTest: false
            })
        }

        function leadingZero(time) {
            return (time < 10 ? '0' : '') + time;
        }

        function getTime() {
            let date = new Date();
            let year = date.getFullYear();
            let day = leadingZero(date.getDate());
            let month = leadingZero((date.getMonth() + 1));
            let hours = leadingZero(date.getHours());
            let minutes = leadingZero(date.getMinutes());
            let seconds = leadingZero(date.getSeconds());
            return year + "-" + month + "-" + day + " " + hours + ":" + minutes + ":" + seconds;
        }

        let speech = window.speechSynthesis;

        this.setState({
            totalPoints: test.length
        })

        if (this.state.totalPoints === 1) {
            this.setState({
                speakTotalQuestions: "pytanie",
                speakTotalPoints: "punkt"
            })
        } else if (this.state.totalPoints === 2) {
            this.setState({
                speakTotalQuestions: "pytania",
                speakTotalPoints: "punkty"
            })
        } else if (this.state.totalPoints === 3) {
            this.setState({
                speakTotalQuestions: "pytania",
                speakTotalPoints: "punkty"
            })
        } else if (this.state.totalPoints === 4) {
            this.setState({
                speakTotalQuestions: "pytania",
                speakTotalPoints: "punkty"
            })
        } else {
            this.setState({
                speakTotalQuestions: "pyta",
                speakTotalPoints: "punktw"
            })
        }

        let choiceMessage = new SpeechSynthesisUtterance(
            `Wybierz prosz sposb rozwizywania testu.
            Kliknij jedynk na klawiaturze numerycznej grnej aby rozwizywa test za pomoc klawiatury 
            lub kliknij dwjk na klawiaturze numerycznej grnej aby uywa wasnej mowy`
        )

        let keyboardOptionMessage = new SpeechSynthesisUtterance(
            `Witaj! Twj nauczyciel wylosowa Ci ${test.length} ${this.state.speakTotalQuestions}.
                Aby odsucha wiadomo powitaln ponownie nacinij klawisz R
                Aby rozpocz test odsuchaj pierwsze pytanie klikajc spacj.
                Po zatwierdzeniu kadej udzielonej odpowiedzi otrzymasz informacj czy bya ona bdna czy poprawna. Kolejne pytanie wywoaj klawiszem spacja.
                Aby porusza si pomidzy odpowiedziami steruj klawiszami W oraz S. 
                Aby wybra odpowied klikaj odpowiednio klawisze A, B, C lub D. 
                Aby zatwierdzi swoj odpowied kliknij klawisz ENTER.
                Aby przerwa mj gos uyj klawisza X.
                Gdy test dobiegnie koca zostaniesz o tym poinformowany!`
        );

        let voiceOptionMessage = new SpeechSynthesisUtterance(
            `Witaj! Twj nauczyciel wylosowa Ci ${test.length} ${this.state.speakTotalQuestions}.
                Aby korzysta z funkcji sterowania gosem musisz mwi pynnie i wyranie. W przypadku nieuzyskania adnej odpowiedzi kliknij ponownie F i powiedz komend.
                Aby odsucha wiadomo powitaln ponownie nacinij F na klawiaturze i powiedz "instrukcja".
                Aby rozpocz test odsuchaj pierwsze pytanie klikajc F na klawiaturze i powiedz "pytanie". 
                Po zatwierdzeniu kadej udzielonej odpowiedzi otrzymasz informacj czy bya ona bdna czy poprawna. Kolejne pytanie wywoaj klikajc przycisk F i powiedz "pytanie".
                Aby porusza si pomidzy odpowiedziami kliknij F i powiedz "kolejna odpowied" lub "poprzednia odpowied". 
                Aby wybra odpowied kliknij klawisz F i powiedz odpowiednio "odpowied a", "odpowied b", "odpowied c", "odpowied d". 
                Aby zatwierdzi swoj odpowied kliknij klawisz F i powiedz "zatwierd".
                Gdy po klikniciu klawisza F i powiedzenia poprawnej komendy nie usyszysz adnej odpowiedzi, musisz powtrzy kliknicie F oraz powiedzenie komendy na nowo.
                Gdy test dobiegnie koca zostaniesz o tym poinformowany!`
        );

        if (this.state.currentQuestion === 0) {
            setTimeout(() => {
                speech.speak(choiceMessage);
            }, 500);
        }

        if (this.state.currentQuestion <= test.length - 1) {

            this.setState({
                body: test[this.state.currentQuestion].body,
                answerA: test[this.state.currentQuestion].answerA,
                answerB: test[this.state.currentQuestion].answerB,
                answerC: test[this.state.currentQuestion].answerC,
                answerD: test[this.state.currentQuestion].answerD,
                properAnswer: test[this.state.currentQuestion].properAnswer,
                category: test[0].category,
                totalPoints: test.length
            })

            let messageBODY = new SpeechSynthesisUtterance(`Pytanie ${this.state.currentQuestion + 1}` + this.state.body);
            let messageANSWER_A = new SpeechSynthesisUtterance('Odpowied A : ' + this.state.answerA);
            let messageANSWER_B = new SpeechSynthesisUtterance('Odpowied B : ' + this.state.answerB);
            let messageANSWER_C = new SpeechSynthesisUtterance('Odpowied C : ' + this.state.answerC);
            let messageANSWER_D = new SpeechSynthesisUtterance('Odpowied D : ' + this.state.answerD);

            let voiceMessages = [];
            voiceMessages.push(messageBODY);
            voiceMessages.push(messageANSWER_A);
            voiceMessages.push(messageANSWER_B);
            voiceMessages.push(messageANSWER_C);
            voiceMessages.push(messageANSWER_D);

            document.body.onkeyup = (e) => {

                if (e.keyCode === 49) {

                    let alert = new SpeechSynthesisUtterance('Wybrae rozwizywanie za pomoc klawiatury');
                    speech.cancel();
                    setTimeout(() => {
                        speech.speak(alert);
                    }, 500);

                    this.setState({
                        option: "keyboard"
                    })
                }

                if (e.keyCode === 50) {

                    let alert = new SpeechSynthesisUtterance('Wybrae rozwizywanie za pomoc gosu');
                    speech.cancel();
                    setTimeout(() => {
                        speech.speak(alert);
                    }, 500);

                    this.setState({
                        option: "voice"
                    })
                }

                if (this.state.option === "keyboard") {

                    if (this.state.currentQuestion === 0) {
                        setTimeout(() => {
                            speech.speak(keyboardOptionMessage);
                        }, 500);
                    }

                    document.body.onkeyup = async (e) => {

                        if (this.state.canSpeak) {

                            if (e.keyCode === 32) {
                                speech.cancel();
                                setTimeout(() => {
                                    speech.speak(voiceMessages[0]);
                                }, 500);
                            }
                            if (e.keyCode === 87) {
                                this.setState({
                                    currentAnswer: this.state.currentAnswer + 1
                                })
                                console.log(this.state.currentAnswer);
                                if (this.state.currentAnswer === 1) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_A);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 2) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_B);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 3) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_C);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 4) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_D);
                                    }, 500);
                                } else {
                                    if (this.state.currentAnswer > 4) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(messageANSWER_A);
                                        }, 500);
                                        this.setState({
                                            currentAnswer: 1
                                        })
                                    }
                                }
                            }
                            if (e.keyCode === 83) {
                                this.setState({
                                    currentAnswer: this.state.currentAnswer - 1
                                })
                                console.log(this.state.currentAnswer);
                                if (this.state.currentAnswer === 1) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_A);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 2) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_B);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 3) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_C);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 4) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_D);
                                    }, 500);
                                } else {
                                    if (this.state.currentAnswer < 1) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(messageANSWER_A);
                                        }, 500);
                                        this.setState({
                                            currentAnswer: 1
                                        })
                                    }
                                }
                            }
                            if (e.keyCode === 65) {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'A',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied A, kliknij enter aby zatwierdzi odpowied'));
                                }, 500);
                            }
                            if (e.keyCode === 66) {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'B',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied B, kliknij enter aby zatwierdzi odpowied'));
                                }, 500);
                            }
                            if (e.keyCode === 67) {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'C',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied C, kliknij enter aby zatwierdzi odpowied'));
                                }, 500);
                            }
                            if (e.keyCode === 68) {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'D',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied D, kliknij enter aby zatwierdzi odpowied'));
                                }, 500);
                            }

                            if (e.keyCode === 82) {
                                speech.cancel();
                                setTimeout(() => {
                                    speech.speak(keyboardOptionMessage);
                                }, 500);
                            }

                        } else {
                            if (e.keyCode === 82) {
                                speech.cancel();
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Test dobieg koca. Interakcja z testem zostaa zablokowana!'));
                                }, 500);
                            }
                        }
                        if (e.keyCode === 88) {
                            speech.cancel();
                        }
                        if (this.state.isAnswered) {
                            if (e.keyCode === 13) {
                                if (this.state.userAnswer === this.state.properAnswer) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(new SpeechSynthesisUtterance('Poprawna odpowied.'));
                                    }, 500);
                                    this.setState({
                                        points: this.state.points + 1,
                                        currentQuestion: this.state.currentQuestion + 1,
                                        isAnswered: false
                                    })
                                    if (this.state.currentQuestion <= test.length - 1) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance('Kliknij spacj aby usysze kolejne pytanie'));
                                        }, 500);
                                        this.serveQuestion(test);
                                    } else {
                                        this.setState({
                                            currentQuestion: this.state.currentQuestion - 1
                                        })
                                        speech.cancel();
                                        let points = this.state.points;
                                        let totalPoints = test.length;
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance(`Koniec testu. Twj wynik to ${points} na ${totalPoints} ${this.state.speakTotalPoints}. Wynik zosta przesany do nauczyciela. Interakcja z testem zostaa zablokowana!`));
                                        }, 500);
                                        let currentDate = getTime();
                                        if (this._isMounted) {
                                            let savingResultProcess = await axios.post('/saveResult', {
                                                login: this.state.login,
                                                category: this.state.category,
                                                points: this.state.points,
                                                totalPoints: totalPoints,
                                                percent: Math.round((this.state.points / test.length) * 100) + '%',
                                                date: currentDate
                                            })
                                            savingResultProcess.data.done && this.props.socket.emit('sendResult', {
                                                login: this.state.login,
                                            })
                                            this.setState({
                                                body: "",
                                                answerA: "",
                                                answerB: "",
                                                answerC: "",
                                                answerD: "",
                                                properAnswer: "",
                                                category: "",
                                                userAnswer: "",
                                                points: 0,
                                                totalPoints: 0,
                                                currentQuestion: 0,
                                                currentAnswer: 0,
                                                isAnswered: false,
                                                canSpeak: false,
                                                canReceiveTest: true
                                            })
                                        }
                                    }
                                } else {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(new SpeechSynthesisUtterance('Bdna odpowied.'));
                                    }, 500);
                                    this.setState({
                                        currentQuestion: this.state.currentQuestion + 1,
                                        isAnswered: false
                                    })
                                    if (this.state.currentQuestion <= test.length - 1) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance('Kliknij spacj aby usysze kolejne pytanie'));
                                        }, 500);
                                        this.serveQuestion(test);
                                    } else {
                                        this.setState({
                                            currentQuestion: this.state.currentQuestion - 1
                                        })
                                        speech.cancel();
                                        let points = this.state.points;
                                        let totalPoints = test.length;
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance(`Koniec testu. Twj wynik to ${points} na ${totalPoints} ${this.state.speakTotalPoints}. Wynik zosta przesany do nauczyciela. Interakcja z testem zostaa zablokowana!`));
                                        }, 500);
                                        let currentDate = getTime();
                                        if (this._isMounted) {
                                            let savingResultProcess = await axios.post('/saveResult', {
                                                login: this.state.login,
                                                category: this.state.category,
                                                points: this.state.points,
                                                totalPoints: totalPoints,
                                                percent: Math.round((this.state.points / test.length) * 100) + '%',
                                                date: currentDate
                                            })
                                            savingResultProcess.data.done && this.props.socket.emit('sendResult', {
                                                login: this.state.login,
                                            })
                                            this.setState({
                                                body: "",
                                                answerA: "",
                                                answerB: "",
                                                answerC: "",
                                                answerD: "",
                                                properAnswer: "",
                                                category: "",
                                                userAnswer: "",
                                                points: 0,
                                                totalPoints: 0,
                                                currentQuestion: 0,
                                                currentAnswer: 0,
                                                isAnswered: false,
                                                canSpeak: false,
                                                canReceiveTest: true
                                            })
                                        }
                                    }
                                }
                            }
                        }
                    }
                }

                if (this.state.option === "voice") {

                    window.SpeechRecognition = window.webkitSpeechRecognition || window.SpeechRecognition;
                    let recognition = new window.SpeechRecognition();
                    recognition.lang = 'pl-PL';

                    if (this.state.currentQuestion === 0) {
                        setTimeout(() => {
                            speech.speak(voiceOptionMessage);
                        }, 500);
                    }

                    document.body.onkeyup = (e) => {
                        if (this.state.canSpeak) {
                            if (e.keyCode === 70) {
                                if (this.state.canHear) {
                                    recognition.start();
                                }
                                this.setState({
                                    canHear: false
                                })
                            }
                            if (e.keyCode === 86) {
                                recognition.stop();
                            }
                        }
                    }

                    recognition.onend = () => {
                        this.setState({
                            canHear: true
                        })
                    }

                    recognition.onresult = async (e) => {

                        let transcript = e.results[0][0].transcript;
                        let lowerCaseTranscript = transcript.toString().toLowerCase();
                        console.log(lowerCaseTranscript);

                        if (this.state.canSpeak) {

                            if (lowerCaseTranscript === "odpowied a") {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'A',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied A, kliknij F na klawiaturze i powiedz "zatwierd" aby zatwierdzi odpowied'));
                                }, 500);
                            }

                            if (lowerCaseTranscript === "odpowied b") {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'B',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied B, kliknij F na klawiaturze i powiedz "zatwierd" aby zatwierdzi odpowied'));
                                }, 500);
                            }

                            if (lowerCaseTranscript === "odpowied c") {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'C',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied C, kliknij F na klawiaturze i powiedz "zatwierd" aby zatwierdzi odpowied'));
                                }, 500);
                            }

                            if (lowerCaseTranscript === "odpowied d") {
                                speech.cancel();
                                this.setState({
                                    userAnswer: 'D',
                                    isAnswered: true
                                })
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Wybrae odpowied D, kliknij F na klawiaturze i powiedz "zatwierd" aby zatwierdzi odpowied'));
                                }, 500);
                            }

                            if (lowerCaseTranscript === "pytanie") {
                                speech.cancel();
                                setTimeout(() => {
                                    speech.speak(voiceMessages[0]);
                                }, 500);
                            }

                            if (lowerCaseTranscript === "kolejna odpowied") {
                                this.setState({
                                    currentAnswer: this.state.currentAnswer + 1
                                })
                                console.log(this.state.currentAnswer);
                                if (this.state.currentAnswer === 1) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_A);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 2) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_B);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 3) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_C);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 4) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_D);
                                    }, 500);
                                } else {
                                    if (this.state.currentAnswer > 4) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(messageANSWER_A);
                                        }, 500);
                                        this.setState({
                                            currentAnswer: 1
                                        })
                                    }
                                }
                            }

                            if (lowerCaseTranscript === "poprzednia odpowied") {
                                this.setState({
                                    currentAnswer: this.state.currentAnswer - 1
                                })
                                console.log(this.state.currentAnswer);
                                if (this.state.currentAnswer === 1) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_A);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 2) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_B);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 3) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_C);
                                    }, 500);
                                }
                                else if (this.state.currentAnswer === 4) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(messageANSWER_D);
                                    }, 500);
                                } else {
                                    if (this.state.currentAnswer < 1) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(messageANSWER_A);
                                        }, 500);
                                        this.setState({
                                            currentAnswer: 1
                                        })
                                    }
                                }
                            }

                            if (lowerCaseTranscript === "instrukcja") {
                                speech.cancel();
                                setTimeout(() => {
                                    speech.speak(voiceOptionMessage);
                                }, 500);
                            }
                        } else {
                            if (lowerCaseTranscript === "instrukcja") {
                                speech.cancel();
                                setTimeout(() => {
                                    speech.speak(new SpeechSynthesisUtterance('Test dobieg koca. Interakcja z testem zostaa zablokowana!'));
                                }, 500);
                            }
                        }

                        if (this.state.isAnswered) {

                            if (lowerCaseTranscript === "zatwierd") {

                                if (this.state.userAnswer === this.state.properAnswer) {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(new SpeechSynthesisUtterance('Poprawna odpowied.'));
                                    }, 500);
                                    this.setState({
                                        points: this.state.points + 1,
                                        currentQuestion: this.state.currentQuestion + 1,
                                        isAnswered: false
                                    })
                                    if (this.state.currentQuestion <= test.length - 1) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance('Kliknij F na klawiaturze i powiedz "pytanie" aby usysze kolejne pytanie'));
                                        }, 500);
                                        this.serveQuestion(test);
                                    } else {
                                        this.setState({
                                            currentQuestion: this.state.currentQuestion - 1
                                        })
                                        speech.cancel();
                                        let points = this.state.points;
                                        let totalPoints = test.length;
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance(`Koniec testu. Twj wynik to ${points} na ${totalPoints} ${this.state.speakTotalPoints}. Wynik zosta przesany do nauczyciela. Interakcja z testem zostaa zablokowana!`));
                                        }, 500);
                                        let currentDate = getTime();
                                        if (this._isMounted) {
                                            let savingResultProcess = await axios.post('/saveResult', {
                                                login: this.state.login,
                                                category: this.state.category,
                                                points: this.state.points,
                                                totalPoints: totalPoints,
                                                percent: Math.round((this.state.points / test.length) * 100) + '%',
                                                date: currentDate
                                            })
                                            savingResultProcess.data.done && this.props.socket.emit('sendResult', {
                                                login: this.state.login,
                                            })
                                            this.setState({
                                                body: "",
                                                answerA: "",
                                                answerB: "",
                                                answerC: "",
                                                answerD: "",
                                                properAnswer: "",
                                                category: "",
                                                userAnswer: "",
                                                points: 0,
                                                totalPoints: 0,
                                                currentQuestion: 0,
                                                currentAnswer: 0,
                                                isAnswered: false,
                                                canSpeak: false,
                                                canReceiveTest: true
                                            })
                                        }
                                    }
                                } else {
                                    speech.cancel();
                                    setTimeout(() => {
                                        speech.speak(new SpeechSynthesisUtterance('Bdna odpowied.'));
                                    }, 500);
                                    this.setState({
                                        currentQuestion: this.state.currentQuestion + 1,
                                        isAnswered: false
                                    })
                                    if (this.state.currentQuestion <= test.length - 1) {
                                        speech.cancel();
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance('Kliknij F na klawiaturze i powiedz "pytanie" aby usysze kolejne pytanie'));
                                        }, 500);
                                        this.serveQuestion(test);
                                    } else {
                                        this.setState({
                                            currentQuestion: this.state.currentQuestion - 1
                                        })
                                        speech.cancel();
                                        let points = this.state.points;
                                        let totalPoints = test.length;
                                        setTimeout(() => {
                                            speech.speak(new SpeechSynthesisUtterance(`Koniec testu. Twj wynik to ${points} na ${totalPoints} ${this.state.speakTotalPoints}. Wynik zosta przesany do nauczyciela. Interakcja z testem zostaa zablokowana!`));
                                        }, 500);
                                        let currentDate = getTime();
                                        if (this._isMounted) {
                                            let savingResultProcess = await axios.post('/saveResult', {
                                                login: this.state.login,
                                                category: this.state.category,
                                                points: this.state.points,
                                                totalPoints: totalPoints,
                                                percent: Math.round((this.state.points / test.length) * 100) + '%',
                                                date: currentDate
                                            })
                                            savingResultProcess.data.done && this.props.socket.emit('sendResult', {
                                                login: this.state.login,
                                            })
                                            this.setState({
                                                body: "",
                                                answerA: "",
                                                answerB: "",
                                                answerC: "",
                                                answerD: "",
                                                properAnswer: "",
                                                category: "",
                                                userAnswer: "",
                                                points: 0,
                                                totalPoints: 0,
                                                currentQuestion: 0,
                                                currentAnswer: 0,
                                                isAnswered: false,
                                                canSpeak: false,
                                                canReceiveTest: true
                                            })
                                        }
                                    }
                                }
                            }
                        }
                    }
                }
            }
        }
    }

    componentDidMount() {
        this._isMounted = true;
        this.props.socket.on('sendTest', (test) => {
            if (this.state.canReceiveTest) {
                this.serveQuestion(test);
            }
        })
    }
    componentWillUnmount() {
        this._isMounted = false;
    }
    render() {
        return (
            <div className="menu">
                <div className="menu-list">
                    <ul className="menu-items">
                        <div className="buttons">
                            <div className="welcome">
                                Welcome {this.props.login}
                            </div>
                            <li><Logout /></li>
                        </div>
                        <div className="blind-student-info">
                            {this.state.body && <div className="alert alert-warning student-info">{`Witaj! Twj nauczyciel wylosowa Ci ${this.state.totalPoints} ${this.state.speakTotalQuestions}`}</div>}
                            {this.state.option === "keyboard" ? <div>
                                <div className="alert alert-warning student-info">Aby odsucha wiadomo powitaln ponownie nacinij klawisz R</div>
                                <div className="alert alert-warning student-info">Aby rozpocz test odsuchaj pierwsze pytanie klikajc spacj.</div>
                                <div className="alert alert-warning student-info">Po zatwierdzeniu kadej udzielonej odpowiedzi otrzymasz informacj czy bya ona bdna czy poprawna. Kolejne pytanie wywoaj klawiszem spacja</div>
                                <div className="alert alert-warning student-info">Aby porusza si pomidzy odpowiedziami steruj klawiszami W oraz S. </div>
                                <div className="alert alert-warning student-info">Aby wybra odpowied klikaj odpowiednio klawisze A, B, C lub D.</div>
                                <div className="alert alert-warning student-info">Aby zatwierdzi swoj odpowied kliknij klawisz ENTER.</div>
                                <div className="alert alert-warning student-info">Aby przerwa czytanie uyj klawisza X.</div>
                                <div className="alert alert-warning student-info">Gdy test dobiegnie koca zostaniesz o tym poinformowany!</div>
                            </div> : this.state.option === "voice" ? <div>
                                <div className="alert alert-warning student-info">Aby korzysta z funkcji sterowania gosem musisz mwi pynnie i wyranie. W przypadku nieuzyskania adnej odpowiedzi kliknij ponownie F i powiedz komend.</div>
                                <div className="alert alert-warning student-info">Aby odsucha wiadomo powitaln ponownie nacinij F na klawiaturze i powiedz "instrukcja".</div>
                                <div className="alert alert-warning student-info">Aby rozpocz test odsuchaj pierwsze pytanie klikajc F na klawiaturze i powiedz "pytanie".</div>
                                <div className="alert alert-warning student-info">Po zatwierdzeniu kadej udzielonej odpowiedzi otrzymasz informacj czy bya ona bdna czy poprawna. Kolejne pytanie wywoaj klikajc przycisk F i powiedz "pytanie".</div>
                                <div className="alert alert-warning student-info">Aby porusza si pomidzy odpowiedziami kliknij F i powiedz "kolejna odpowied" lub "poprzednia odpowied".</div>
                                <div className="alert alert-warning student-info">Aby wybra odpowied kliknij klawisz F i powiedz odpowiednio "odpowied a", "odpowied b", "odpowied c", "odpowied d".</div>
                                <div className="alert alert-warning student-info">Aby zatwierdzi swoj odpowied kliknij klawisz F i powiedz "zatwierd".</div>
                                <div className="alert alert-warning student-info">Gdy po klikniciu klawisza F i powiedzenia poprawnej komendy nie usyszysz adnej odpowiedzi, musisz powtrzy kliknicie F oraz powiedzenie komendy na nowo.</div>
                                <div className="alert alert-warning student-info">Gdy test dobiegnie koca zostaniesz o tym poinformowany!</div>
                            </div> : <div>
                                        <div className="alert alert-success student-info">Instrukcje dla opcji rozwizywania testu za pomoc klawiatury</div>
                                        <div className="alert alert-warning student-info">Aby odsucha wiadomo powitaln ponownie nacinij klawisz R</div>
                                        <div className="alert alert-warning student-info">Aby rozpocz test odsuchaj pierwsze pytanie klikajc spacj.</div>
                                        <div className="alert alert-warning student-info">Po zatwierdzeniu kadej udzielonej odpowiedzi otrzymasz informacj czy bya ona bdna czy poprawna. Kolejne pytanie wywoaj klawiszem spacja</div>
                                        <div className="alert alert-warning student-info">Aby porusza si pomidzy odpowiedziami steruj klawiszami W oraz S. </div>
                                        <div className="alert alert-warning student-info">Aby wybra odpowied klikaj odpowiednio klawisze A, B, C lub D.</div>
                                        <div className="alert alert-warning student-info">Aby zatwierdzi swoj odpowied kliknij klawisz ENTER.</div>
                                        <div className="alert alert-warning student-info">Aby przerwa czytanie uyj klawisza X.</div>
                                        <div className="alert alert-warning student-info">Gdy test dobiegnie koca zostaniesz o tym poinformowany!</div>
                                        <div className="alert alert-success student-info">Instrukcje dla opcji rozwizywania testu za pomoc gosu</div>
                                        <div className="alert alert-warning student-info">Aby korzysta z funkcji sterowania gosem musisz mwi pynnie i wyranie. W przypadku nieuzyskania adnej odpowiedzi kliknij ponownie F i powiedz komend.</div>
                                        <div className="alert alert-warning student-info">Aby odsucha wiadomo powitaln ponownie nacinij F na klawiaturze i powiedz "instrukcja".</div>
                                        <div className="alert alert-warning student-info">Aby rozpocz test odsuchaj pierwsze pytanie klikajc F na klawiaturze i powiedz "pytanie".</div>
                                        <div className="alert alert-warning student-info">Po zatwierdzeniu kadej udzielonej odpowiedzi otrzymasz informacj czy bya ona bdna czy poprawna. Kolejne pytanie wywoaj klikajc przycisk F i powiedz "pytanie".</div>
                                        <div className="alert alert-warning student-info">Aby porusza si pomidzy odpowiedziami kliknij F i powiedz "kolejna odpowied" lub "poprzednia odpowied".</div>
                                        <div className="alert alert-warning student-info">Aby wybra odpowied kliknij klawisz F i powiedz odpowiednio "odpowied a", "odpowied b", "odpowied c", "odpowied d".</div>
                                        <div className="alert alert-warning student-info">Aby zatwierdzi swoj odpowied kliknij klawisz F i powiedz "zatwierd".</div>
                                        <div className="alert alert-warning student-info">Gdy po klikniciu klawisza F i powiedzenia poprawnej komendy nie usyszysz adnej odpowiedzi, musisz powtrzy kliknicie F oraz powiedzenie komendy na nowo.</div>
                                        <div className="alert alert-warning student-info">Gdy test dobiegnie koca zostaniesz o tym poinformowany!</div>
                                    </div>}
                        </div>
                    </ul>
                </div>
                <div className="questions blindStudentQuestions center">
                    {this.state.body === "" ? <div>Please wait...</div> : <div className="question blindStudentQuestion">
                        <div className="body alert alert-dark font-weight-bold">{this.state.currentQuestion + 1 + ". " + this.state.body}</div>
                        <div className="aswer alert alert-success">{"A. " + this.state.answerA}</div>
                        <div className="aswer alert alert-success">{"B. " + this.state.answerB}</div>
                        <div className="aswer alert alert-success">{"C. " + this.state.answerC}</div>
                        <div className="aswer alert alert-success">{"D. " + this.state.answerD}</div>
                    </div>}
                </div>
            </div>
        )
    }
}

export default BlindStudentHome